import { Component } from "react";
import TiroCompiler, { createCompiler, CompilerResult } from "../TiroCompiler";
import { CompilerView } from "../components/CompilerView";

interface PlaygroundState {
    compiling: boolean;
    currentSource: string;
    compiler?: TiroCompiler;
    result?: CompilerResult;
}

const INITIAL_SOURCE = `
import std;

func foo() {
    for (var i = 1; i <= 10; i += 1) {
        std.print(i);
    }
}
`.trimLeft();

function asyncSetState<T extends Component, S>(
    comp: Component,
    newState: S
): Promise<void> {
    return new Promise((resolve) => {
        comp.setState(newState, resolve);
    });
}

export default class Playground extends Component<{}, PlaygroundState> {
    constructor(props: any) {
        super(props);
        this.state = {
            compiling: false,
            currentSource: INITIAL_SOURCE,
        };
    }

    async componentDidMount(): Promise<void> {
        let compiler;
        try {
            compiler = await createCompiler();
        } catch (e) {
            console.error("Failed to create compiler instance", e);
            return;
        }

        await asyncSetState(this, { compiler });
        this._startCompilation(this.state.currentSource);
    }

    componentWillUnmount(): void {
        this.state.compiler?.destroy();
    }

    private async _startCompilation(source: string): Promise<void> {
        // TODO: Async compilation api (Web worker?)
        try {
            const { compiler } = this.state;
            await asyncSetState(this, { compiling: true });
            const result = compiler?.compile(source);
            await asyncSetState(this, { compiling: false, result });
        } catch (e) {
            // TODO: Error state in UI
            console.error("Compilation failed", e);
        }
    }

    render(): JSX.Element {
        const { compiling, currentSource, compiler, result } = this.state;
        if (!compiler || !result) {
            return <div>Loading...</div>;
        }

        return (
            <CompilerView
                compiling={compiling}
                initialSource={currentSource}
                result={result}
                onSourceChanged={this._handleSourceChanged}
            ></CompilerView>
        );
    }

    private _handleSourceChanged = async (value: string): Promise<void> => {
        await asyncSetState(this, { currentSource: value });
        this._startCompilation(value);
    };
}
